import {InferActionsTypes} from "./redux-store";

export const dialogsActions = {
  sendMessage: (myMessage: string) => ({
    type: "dialogs/ADD-MESSAGE",
    myMessage: myMessage
  } as const)
}

const initialState = {
  dialogs : [
    { name: "Sasha", id: 1, ava: "data:image/jpeg;base64,"},
    { name: "Dima", id: 2, ava: "data:image/jpeg;base64,"},
    { name: "Kiborg", id: 3, ava: "data:image/jpeg;base64,"},
    { name: "Kion", id: 4, ava: "data:image/jpeg;base64,"},
    { name: "Masya", id: 5, ava: "data:image/jpeg;base64,"}
  ] as Array<DialogsType>,
  messages: [
    {id: 1, message: "Hello, how are you?"},
    {id: 2, message: "I'm fine, thank you"},
    {id: 3, message: "Yo"},
    {id: 4, message: "Yo"},
    {id: 5, message: "Yo"}
  ] as Array<MessagesType>
}

const dialogsReducer = (state = initialState, action: ActionsType): InitialStateType => {
  switch (action.type) {
    case "dialogs/ADD-MESSAGE": {
      return {...state,
        messages: [...state.messages, {id: state.messages.length + 1, message: action.myMessage}]
      };
    }
    default:
      return state;
  }
}

export default dialogsReducer;


export type InitialStateType = typeof initialState;

type ActionsType = InferActionsTypes<typeof dialogsActions>

type DialogsType = {
  name: string
  id: number
  ava: string
}

type MessagesType = {
  id: number
  message: string
}
