const initialState = {
    friends: [
        { name: "Andrew", id: 1, photoUrl: "data:image/jpeg;base64,"},
        { name: "Sasha", id: 2, photoUrl: "data:image/jpeg;base64,"},
        { name: "Sveta", id: 3, photoUrl: "data:image/jpeg;base64,"}
    ] as Array<FriendsArrayType>
}

const sidebarReducer = (state = initialState, action: any): InitialStateType => {
  return state;
}

export default sidebarReducer;


export type FriendsArrayType = {
    name: string
    id: number
    photoUrl: string
}

type InitialStateType = typeof initialState;
